.btn {
    padding: 10px 20px;
    color: $txt-main-color;
    font-weight: 600;
    font-size: 20px;
    text-transform: uppercase;
    border-radius: 4px;
    border: 3px solid $txt-main-color;
    background: transparent;
    cursor: pointer;
}

.btn:hover {
    box-shadow: $box-shadow;
}